.app {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
} 

.bioContainer {
  display: flex;
  width: 50%;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  padding: 0 1.5rem;
  background-color: black;
  font-family: "Nunito", -apple-system, BlinkMacSystemFont;
  color: beige;
}

.socialContainer {
  display: flex;
  width: 50%;
  flex-direction: column;
}

.profile {
  width: 210px;
  height: 210px;
  align-self: center;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border: 6px solid white;
}

@media only screen and (max-width: 800px) {
  .hide-if-small {
      display: none;
  }
}

@media only screen and (min-width: 800px) {
  .show-if-small {
      display: none;
  }
}
